import React from 'react';

const dimension = 200;

const Card = ({ name, email, id }) => {
  return (
    <div className='tc grow bg-light-green br3 pa3 ma2 dib bw2 shadow-5'>
      <img alt='robots' width="{dimension}" height="{dimension}" src={`https://robohash.org/${id}?size=${dimension}x${dimension}`} />
      <div>
        <h2>{name}</h2>
        <p>{email}</p>
      </div>
    </div>
  );
}

export default Card;